import { Utils } from '../../utils/Utils';
import { Rect } from '../../canvas/Rect';
import { CellsBorder } from './CellsBorder';
import { ALIGN, TEXT_DIRECTION, TEXT_WRAP, VERTICAL_ALIGN } from '../../canvas/Font';
import { LINE_TYPE } from '../../canvas/Line';

class Cells extends CellsBorder {

  constructor({
    table, cols, rows, data = [],
  }) {
    super();
    this.table = table;
    this.cols = cols;
    this.rows = rows;
    this._ = data;
  }

  static getDefaultAttr() {
    const fontAttr = {
      align: ALIGN.left,
      verticalAlign: VERTICAL_ALIGN.center,
      textWrap: TEXT_WRAP.OVER_FLOW,
      strikethrough: false,
      underline: false,
      color: 'rgb(0,0,0)',
      name: 'Arial',
      size: 14,
      bold: false,
      italic: false,
      direction: TEXT_DIRECTION.HORIZONTAL,
    };
    const borderAttr = {
      time: Utils.now(),
      left: { display: false, width: 1, color: '#000000', type: LINE_TYPE.SOLID_LINE },
      top: { display: false, width: 1, color: '#000000', type: LINE_TYPE.SOLID_LINE },
      right: { display: false, width: 1, color: '#000000', type: LINE_TYPE.SOLID_LINE },
      bottom: { display: false, width: 1, color: '#000000', type: LINE_TYPE.SOLID_LINE },
    };
    return {
      ID: Utils.now(),
      text: '',
      format: 'default',
      background: null,
      fontAttr,
      borderAttr,
    };
  }

  checkAttributeIntegrity(cell) {
    return Utils.isUnDef(cell.ID) ? Utils.mergeDeep(Cells.getDefaultAttr(), cell) : cell;
  }

  getCellHorizontalOverFlowRect(ri, ci) {
    const cell = this.getCell(ri, ci);
    const { cols } = this;
    const { fontAttr } = cell;
    const { align } = fontAttr;
    let width = 0;
    let offset = 0;
    if (align === ALIGN.left) {
      for (let i = ci, { len } = cols; i <= len; i += 1) {
        const cell = this.getCell(ri, i);
        if (i === ci) {
          width += cols.getWidth(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          width += cols.getWidth(i);
        } else {
          break;
        }
      }
    }
    if (align === ALIGN.center) {
      for (let i = ci, { len } = cols; i <= len; i += 1) {
        const cell = this.getCell(ri, i);
        if (i === ci) {
          width += cols.getWidth(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          width += cols.getWidth(i);
        } else {
          break;
        }
      }
      for (let i = ci - 1; i >= 0; i -= 1) {
        const cell = this.getCell(ri, i);
        if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          const tmp = cols.getWidth(i);
          width += tmp;
          offset -= tmp;
        } else {
          break;
        }
      }
    }
    if (align === ALIGN.right) {
      for (let i = ci; i >= 0; i -= 1) {
        const cell = this.getCell(ri, i);
        if (i === ci) {
          width += cols.getWidth(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          const tmp = cols.getWidth(i);
          width += tmp;
          offset -= tmp;
        } else {
          break;
        }
      }
    }
    return { width, offset };
  }

  getCellVerticalOverFlowRect(ri, ci) {
    const cell = this.getCell(ri, ci);
    const { rows } = this;
    const { fontAttr } = cell;
    const { verticalAlign } = fontAttr;
    let height = 0;
    let offset = 0;
    if (verticalAlign === VERTICAL_ALIGN.top) {
      for (let i = ri, { len } = rows; i <= len; i += 1) {
        const cell = this.getCell(i, ci);
        if (i === ci) {
          height += rows.getHeight(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          height += rows.getHeight(i);
        } else {
          break;
        }
      }
    }
    if (verticalAlign === VERTICAL_ALIGN.center) {
      for (let i = ri, { len } = rows; i <= len; i += 1) {
        const cell = this.getCell(i, ci);
        if (i === ci) {
          height += rows.getHeight(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          height += rows.getHeight(i);
        } else {
          break;
        }
      }
      for (let i = ri - 1; i >= 0; i -= 1) {
        const cell = this.getCell(ri, i);
        if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          const tmp = rows.getHeight(i);
          height += tmp;
          offset -= tmp;
        } else {
          break;
        }
      }
    }
    if (verticalAlign === VERTICAL_ALIGN.bottom) {
      for (let i = ri; i >= 0; i -= 1) {
        const cell = this.getCell(ri, i);
        if (i === ci) {
          height += rows.getHeight(i);
        } else if (Utils.isUnDef(cell) || Utils.isBlank(cell.text)) {
          const tmp = rows.getHeight(i);
          height += tmp;
          offset -= tmp;
        } else {
          break;
        }
      }
    }
    return { height, offset };
  }

  getCell(ri, ci) {
    const row = this._[ri];
    if (row && row[ci]) {
      row[ci] = this.checkAttributeIntegrity(row[ci]);
      return row[ci];
    }
    return null;
  }

  getMergeCellOrCell(ri, ci) {
    const { table } = this;
    const { merges } = table;
    const merge = merges.getFirstIncludes(ri, ci);
    if (merge) {
      return this.getCell(merge.sri, merge.sci);
    }
    return this.getCell(ri, ci);
  }

  getCellOrNew(ri, ci) {
    if (!this._[ri]) {
      this._[ri] = [];
    }
    if (!this._[ri][ci]) {
      this._[ri][ci] = {};
    }
    this._[ri][ci] = this.checkAttributeIntegrity(this._[ri][ci]);
    return this._[ri][ci];
  }

  getCellInRectRange(rectRange, cb, createNew = false, { sy = 0, sx = 0 } = {}) {
    const {
      sri, eri, sci, eci,
    } = rectRange;
    let y = sy;
    for (let i = sri; i <= eri; i += 1) {
      const height = this.rows.getHeight(i);
      let x = sx;
      for (let j = sci; j <= eci; j += 1) {
        const width = this.cols.getWidth(j);
        let cell = null;
        if (createNew) {
          cell = this.getCellOrNew(i, j);
        } else {
          cell = this.getCell(i, j);
        }
        if (cell) {
          const { fontAttr } = cell;
          const { direction, textWrap } = fontAttr;
          const rect = new Rect({
            x,
            y,
            width,
            height,
          });
          let overFlow = null;
          if (textWrap === TEXT_WRAP.OVER_FLOW) {
            if (direction === TEXT_DIRECTION.VERTICAL) {
              const result = this.getCellVerticalOverFlowRect(i, j);
              overFlow = new Rect({ x, y: y + result.offset, width, height: result.height });
            } else {
              const result = this.getCellHorizontalOverFlowRect(i, j);
              overFlow = new Rect({ x: x + result.offset, y, width: result.width, height });
            }
          }
          const result = cb(i, j, cell, rect, overFlow);
          if (result === false) {
            return;
          }
        }
        x += width;
      }
      y += height;
    }
  }

  getMergeCellInViewRange(viewRange, cb) {
    const { table } = this;
    const { merges, cols, rows } = table;
    const filter = [];
    this.getCellInRectRange(viewRange, (i, c) => {
      const rectRange = merges.getFirstIncludes(i, c);
      if (!rectRange || filter.find(item => item === rectRange)) return;
      filter.push(rectRange);
      const cell = this.getCell(rectRange.sri, rectRange.sci);
      const minSri = Math.min(viewRange.sri, rectRange.sri);
      let maxSri = Math.max(viewRange.sri, rectRange.sri);
      const minSci = Math.min(viewRange.sci, rectRange.sci);
      let maxSci = Math.max(viewRange.sci, rectRange.sci);
      maxSri -= 1;
      maxSci -= 1;
      let x = cols.sectionSumWidth(minSci, maxSci);
      let y = rows.sectionSumHeight(minSri, maxSri);
      x = viewRange.sci > rectRange.sci ? x * -1 : x;
      y = viewRange.sri > rectRange.sri ? y * -1 : y;
      const width = cols.sectionSumWidth(rectRange.sci, rectRange.eci);
      const height = rows.sectionSumHeight(rectRange.sri, rectRange.eri);
      const rect = new Rect({
        x,
        y,
        width,
        height,
      });
      cb(rect, cell);
    });
  }

  getCellInViewRange(rectRange, cb, createNew = false, { sy = 0, sx = 0 } = {}) {
    const { table } = this;
    const { merges } = table;
    this.getCellInRectRange(rectRange, (i, j, cell, rect, overflow) => {
      const merge = merges.getFirstIncludes(i, j);
      if (merge === null) {
        cb(i, j, cell, rect, overflow);
      }
    }, createNew, { sy, sx });
  }

  getData() {
    return this._;
  }

  setData(data = []) {
    this._ = data;
    return this;
  }
}

export { Cells };
